$text-color: #6e6e6e;

.translator-text-area-container {
    position: relative;
    // background: #343434;
    // box-shadow: 0 0 1em #1b1b18;
    border: 1px solid #dbdbdb;
    border-radius: 0px 0 4px 4px;
    // min-width: 464px;
    display: flex;

    .translator-text-area {
        width: 100%;
        height: 250px;
        resize: none;
        border: none;
        background: transparent;
        outline: none;
        color: $text-color;
        font-size: 20px;
        padding: 7px;
    }

    .close-icon {
        cursor: pointer;
        color: $text-color;
        font-size: 23px;
        margin: 8px 11px 12px 3px;
    }

    .copy-container {
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        position: absolute;
        bottom: 20px;
        right: 10px;
        width: 100px;
        color: #6e6e6e;
        font-size: 17px;

        span {
            cursor: pointer;
        }

        .copy-icon {
            cursor: pointer;
        }
    }

    @media only screen and (max-width: 550px) {
        border-radius: unset;

        .translator-text-area {
            padding: 0;
        }
    }

    @media only screen and (max-width: 719px) {
        max-height: 250px;
        max-width: 100%;
        min-width: auto;
    }
}
